export const FileTypeUsingProp = (props) => {
  if (props.type == "text") {
    return <FiletypeText />;
  }
  if (props.type == "video") {
    return <FiletypeVideo />;
  }
  if (props.type == "audio") {
    return <FiletypeAudio />;
  }
  if (props.type == "question") {
    return <FiletypeQue />;
  }
};

